.card-list {
    color: brown;
    width: 70vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 16px;
    font-family: "Margarine", sans-serif;
}

.monster-heading {
    color: rgba(226, 43, 98, 0.894);
    margin-top: 0px;
    font-family: 'Bigelow Rules', serif;
    font-size: 100px;
    font-weight: 800;
    text-shadow: 2pt 2pt #555555;
}