.card-container {
    display: flex;
    flex-direction: column;
    background: linear-gradient(#c0d0e0, #ffb6c1);
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.10s ease-out;
}

.card-container:hover {
    transform: scale(1.1);
    background: linear-gradient(#b0e0e6, #ffb6c1);
    background-blend-mode: color-dodge;
    color: rgb(23, 45, 159);
}